//1. GET DATA FROM ELEMENT

export function displayMap(locationsCurrent) {
  //2. Convert from [lat, lng] to [lng, lat]
  let markers = [];
  locationsCurrent.forEach(el => {
    const [lat, lng] = el.coordinates;
    markers.push({
      coordinates: [lng, lat],
      description: el.description,
      day: el.day
    });
  });

  //3. Initial locations map
  let map = L.map('map').setView(markers[0].coordinates, 8);

  L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
    attribution:
      '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
  }).addTo(map);

  markers.forEach(marker => {
    const tooltip = L.tooltip({
      direction: 'top', // Hng hin th ca tooltip
      permanent: true, // Hin th tooltip lun, khng bin mt sau khi di chut ra khi marker
      className: 'custom-tooltip', // Class CSS ty chnh cho tooltip
      offset: [0, -20] // Dch chuyn v tr ca tooltip so vi marker
    }).setContent(`${marker.description}- ${marker.day} day`);

    L.marker(marker.coordinates)
      .addTo(map)
      .bindTooltip(tooltip)
      .openTooltip();
  });

  function handleLogin(e) {
    e.preventDefault(); // Ngn chn vic gi form mc nh

    // Ly gi tr t cc trng nhp liu
    const email = document.getElementById('email').value;
    const password = document.getElementById('password').value;
    // console.log('data', { email, password });
    //Gi yu cu ng nhp n my ch
    fetch('/api/v1/login', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify({ email, password })
    })
      .then(response => {
        if (response.status === 'success') {
          // ng nhp thnh cng, chuyn hng n trang chnh
          window.location.href = '/';
        } else {
          // ng nhp tht bi, hin th thng bo li
          alert(
            'ng nhp tht bi. Vui lng kim tra li thng tin ng nhp.'
          );
        }
      })
      .catch(error => {
        console.error('Li khi gi yu cu ng nhp:', error);
        alert(' xy ra li. Vui lng th li sau.');
      });
  }
}
